.alert {
    position: fixed;
    z-index: 9999;
    top: 0;
    right: 0;
    left: 0;
    display: flex;
    width: 100vw;
    max-width: 100vw;
    height: 50px;
    align-items: center;
    padding: 0 50px;
    font-size: 2rem;

    &__content {
        width: 100vw;
        max-width: 1920px;
        margin: auto;
    }

    &__message {
        display: flex;
        width: 100%;

        p {
            width: 100%;
            margin-left: 60px;
            color: white;
            font-family: Lato, Arial, Helvetica, sans-serif;
        }

        .loading {
            top: 10px;
            width: auto;

            svg {
                font-size: 3rem;
            }
        }
    }
}
